.toggle{
    display: flex;
    width: 40px;
    justify-content:space-between;
    border: 3px solid var(--orange);
    border-radius: 16px;
    position: relative;
    padding: 2px;
    cursor: pointer;
}
.toggle>*{
    width: 16px;
    height: 16px;
    color: var(--orange);

}
.t-button{
    border-radius: 100%;
    background: var(--orange);
    position: absolute;
}