.floatingdiv{
    display: flex;
    justify-content: space-around;
    background: white;
    box-shadow: var(--boxShadow);
    border-radius: 17px;
    align-items: center ;
    height: 80px;
    padding: 0px 28px 0px 0px;
}
.floatingdiv>img{
    transform: scale(0.4 );
}
.floatingdiv >span{
     font-family: sans-serif;
     font-size: 16px;
}
